<template>
    <b-card>
        <div class="text-center" v-if="loading.main">
            <b-spinner variant="primary" style="height:10rem;width:10rem"></b-spinner>
        </div>
        <b-row v-if="!loading.main">
            <b-col sm="12">
                <div class="d-flex">
                    <div>
                        <span class="h3">{{`${info.first_name} ${info.last_name}`}}</span>
                        <span class="text-secondary">
                            {{`${info.country.name}, ${info.state.name}, ${info.city.name}`}}
                        </span>
                    </div>
                    <div class="text-secondary ml-auto">
                        <span>Joined: {{ info.created_at | moment("dddd, MMMM Do YYYY") }}</span>
                    </div>
                </div>

                <div>
                    <span class="text-primary">{{info.email}}</span>
                </div>
                <div>
                    <span class="text-primary">{{`(+${info.phone_code}) ${info.phone}`}}</span>
                </div>

                <hr>
                <h2>Orders</h2>
                <b-row>
                    <b-col sm="6" v-for="order in info.order" :key="order.id">
                        <b-card class="mt-1" >
                            <template #header>
                                <div class="d-flex">
                                    <h4>{{order.title}}</h4>
                                    <div class="ml-auto">
                                        <span :class="{'text-success':order.status == 'completed', 'text-info':order.status == 'processing', 'text-warning': order.status == 'review'}">{{order.status}}</span>
                                    </div>
                                </div>
                            </template>
                            <h5 class="text-gery">Order Details</h5>
                            <b-row>
                                <b-col sm="4" class="text-right text-gery">
                                    Description:
                                </b-col>
                                <b-col sm="8">
                                    {{order.description}}
                                </b-col>

                                <b-col sm="4" class="text-right text-gery">
                                    Price:
                                </b-col>
                                <b-col sm="8" class="h3 ">
                                    {{order.price}}
                                </b-col>
                            </b-row>
                            <hr>
                            <h5 class="text-gery">Recipient Details</h5>
                            <b-row v-for="rec in order.recipient" :key="rec.id">
                                <b-col sm="4" class="text-right text-gery">
                                    Name:
                                </b-col>
                                <b-col sm="8">
                                    {{`${rec.first_name} ${rec.last_name}`}}
                                </b-col>

                                <b-col sm="4" class="text-right text-gery">
                                    Address:
                                </b-col>
                                <b-col sm="8">
                                    {{rec.address}}
                                </b-col>

                                <b-col sm="4" class="text-right text-gery">
                                    Phone:
                                </b-col>
                                <b-col sm="8">
                                    {{`(${rec.phone_code}) ${rec.phone}`}}
                                </b-col>
                            </b-row>
                            <template #footer>
                                <div class="d-flex">
                                    <b-button size="sm" variant="success" @click="$router.push({name:'Edit Order', params:{id:order.id}})">Edit</b-button>
                                    <div class="ml-auto text-gery">
                                        {{ order.created_at | moment("from", "now") }}
                                    </div>
                                </div>
                            </template>
                        </b-card>
                    </b-col>
                </b-row>
            </b-col> 
        </b-row>
    </b-card>
</template>
<script>
import Vue from 'vue'
Vue.use(require('vue-moment'));
import axios from "axios"

export default {
    name:"Profile",
    data() {
        return { 
            info:{country:{},state:{},},
            loading:{
                main:false,
            }
        }
    },
    methods:{
        getUserData() {
            this.loading.main = true
            axios.get(`/admin/v1/users/${this.$route.params.id}`)
            .then(res=>{
                this.info = res.data.data;
                this.loading.main = false
            })
            .catch(()=>{
                this.loading.main = false
            })
        }
    },
    mounted() {
        this.getUserData()
    }
}
</script>
<style >
.text-gery {
    color:#999
}
</style>